.dashboard-container {
    padding: 60px;
    max-width: 1400px;
    margin: 0 auto;
  }
  
  /* Title Section */
  .title-section {
    margin-bottom: 60px;
  }
  
  .title-section h1 {
    font-size: 48px;
    font-weight: bold;
    line-height: 1.2;
    margin-bottom: 16px;
  }
  
  .company-name {
    color: #666;
    font-size: 18px;
  }
  
  /* Media Section */
  .media-section {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 100px;
  }
  
  .project-media {
    width: 50%;
    height: 400px;
    background: #E5E7EB;
    border-radius: 12px;
    overflow: hidden;
  }
  
  .project-video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  }
  
  /* Goal Section */
  .content-section {
      max-width: 800px;
      margin: 0 auto;
      opacity: 0;
      animation: fadeInBlur 0.8s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  }
    
    /* Add delay for subsequent sections */
    .content-section:nth-child(2) {
      animation-delay: 0.2s;
   }
    
    .content-section:nth-child(3) {
      animation-delay: 0.4s;
  }
    
    .content-section:nth-child(4) {
      animation-delay: 0.6s;
  }  
  
  .content-section h2 {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 5px;
  }
  
  .underline {
    height: 1px;
    background-color: #000;
    width: 100%;
    margin-bottom: 20px;
  }
  
  .content-section p {
    color: #666;
    line-height: 1.6;
    font-size: 16px;
  }
  
  .content-section + .content-section {
    margin-top: 100px;
  }
  
  .two-column-content {
    display: flex;
    gap: 60px;
    margin-top: 20px;
  }
  
  .column {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .column h3 {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 20px;
  }
  
  .column p {
    color: #666;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .column p:last-child {
    margin-bottom: 0;
  }
  
  .video-placeholder {
    width: 100%;
    height: 400px;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  
  .tech-icons {
    display: flex;
    gap: 20px;
    margin-bottom: 30px;
    justify-content: center;
    align-items: center;
  }
  
  .tech-icon {
    width: 150px;
    height: 150px;
    object-fit: contain;
  }
  
  .tech-icon[alt="Python"],
  .tech-icon[alt="React"],
  .tech-icon[alt="MongoDB"],
  .tech-icon[alt="JavaScript"],
  .tech-icon[alt="AWS"]
  {
    border: 1px solid black;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  
  /* Add back button styling */
  .back-button-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 60px;
  }
  
  .back-button {
    background-color: #000;
    color: #fff;
    padding: 12px 24px;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
  }
  
  .back-button:hover {
    background-color: #333;
    transform: translateY(-2px);
  }
  
  /* Add fade-in animation */
  @keyframes fadeInBlur {
    0% {
      opacity: 0;
      filter: blur(20px);
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      filter: blur(0);
      transform: translateY(0);
    }
  }
  
  /* Add fade-in classes */
  .fade-in {
    opacity: 0;
    transform: translateY(100px);
    visibility: hidden;
    transition: opacity 1s cubic-bezier(0.4, 0, 0.2, 1),
                transform 1s cubic-bezier(0.4, 0, 0.2, 1),
                visibility 1s cubic-bezier(0.4, 0, 0.2, 1);
    will-change: opacity, visibility, transform;
  }
  
  .fade-in.is-visible {
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }
  
  /* Add fade to media sections */
  .media-section {
    opacity: 0;
    animation: fadeInBlur 0.8s cubic-bezier(0.16, 1, 0.3, 1) 0.2s forwards;
  }
  
  .video-placeholder {
    opacity: 0;
    animation: fadeInBlur 0.8s cubic-bezier(0.16, 1, 0.3, 1) 0.4s forwards;
  }
  
  .email-image-container {
    width: 150%;
    margin-left: -25%;
    margin-top: 40px;
    margin-bottom: 40px;
  }
  
  .email-image {
    width: 100%;
    height: auto;
    border-radius: 12px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  } 